dl, ol, ul {
    margin-top: 0;
    margin-bottom: 1rem;
    list-style: none;
    margin: 0px;
    padding: 0px;
}
a{
    text-decoration: none;
    color: inherit;
}
a:hover{
    text-decoration: none;
    color: inherit;
}

input:focus, button:focus{
    -webkit-focus-ring-color:0px;
}

.header{
    width: 100%;
    background-color: #fff;
}
.container{
    padding: 0px !important;
}
@media (min-width: 1200px){
    .container, .container-lg, .container-md, .container-sm, .container-xl {
        max-width: 1200px;
    }
}

.header .header-main{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.header .top-header{
    height: 40px;
    width: 100%;
