}
.nav-global .ng-item-wrap{
    padding: 0 5px;
}
.nav-global .ng-item {
    position: relative;
    float: left;
    height: 39px;
    line-height: 39px;
}
.nav-global .ng-sub-title, .nav-global .ng-switcher .switcher-info {
    position: relative;
    display: block;
    padding: 0 8px;
