}
.nav-global .ng-mobile a:before {
    background-image: url('../../assets/icons.png');
    background-position: 0 -923px;
    background-repeat: no-repeat;
}
.nav-global .ng-mobile a:before {
    content: "";
    position: absolute;
    top: 12px;
    left: -10px;
    width: 16px;
    height: 16px;
    overflow: hidden;
}
.nav-global .nav-wishlist {
    padding: 0 8px;
}
.nav-global .ng-item-wrap {
    float: left;
    position: relative;
    padding: 0 4px;
}
.nav-global .ng-icon-size {
    display: inline-block;
    width: 16px;
    height: 16px;
    vertical-align: middle;
}
.nav-global .nav-wishlist .text {
    margin-left: 4px;
}
.nav-global .ng-wishlist-icon {
    background-image: url('../../assets/icons.png');
