    background-repeat: no-repeat;
}
.nav-global .ng-item:last-child {
    border-right: 0;
}
.nav-user-account .user-account-port {
    display: inline-block;
    position: relative;
    padding: 0 8px;
}
.nav-global .user-account-port>a {
    padding-right: 16px;
}
.nav-user-account .user-account-port .text {
    margin-left: 4px;
    padding-right: 10px;
}
.header-navigation li.nav-sub>a:after, .hm-left .store-container h3.store-name:after, .nav-global .ng-sub-title:after, .nav-global .ng-switcher .switcher-info:after, .nav-global .user-account-port>a:after, .searchbar-form .search-category:after, .store-container.active h3.store-name:after {
    background-image: url('../../assets/icons.png');
    background-position: 0 -1271px;
    background-repeat: no-repeat;
}
.nav-global .user-account-main {
    position: absolute;
    font-size: 13px;
    display: none;
    right: -1px;
    top: 40px;
    padding-top: 15px;
    width: 242px;
    border: 1px solid #e8e8e8;
    background-color: #fff;
    color: #656565;
    z-index: 12;
    box-shadow: 0 2px 8px 0 rgb(0 0 0 / 10%);
}
.nav-global .user-account-main .flyout-user-signIn {
    padding: 0 16px;
}
.nav-user-account .flyout-new-user .flyout-welcome-wrap {
    line-height: 20px;
    display: block;
}
.nav-user-account .flyout-new-user .flyout-logined, .nav-user-account .flyout-new-user .flyout-sign-out {
    display: none;
}
.nav-user-account .flyout-user-avatar {
    width: 32px;
    height: 32px;
    vertical-align: middle;
    background: orange;
    border-radius: 50%;
