    display: inline-block;
}
.nav-user-account .flyout-user-avatar img {
    max-width: 100%;
    vertical-align: top;
}
.nav-user-account .flyout-welcome-text {
    width: 164px;
    vertical-align: middle;
    display: inline-block;
    line-height: 16px;
    font-weight: 700;
    margin-left: 4px;
}
.flyout-user-signIn a.join-btn {
    background-color: #ff4747;
    color: #fff;
}
.nav-global .ng-item a {
    color: #333;
}
.flyout-user-signIn .join-btn, .flyout-user-signIn .sign-btn {
    border-radius: 2px;
    font-weight: 700;
    padding: 6px 0;
    width: 100px;
    text-align: center;
    display: inline-block;
    line-height: 20px;
}
.flyout-user-signIn a.sign-btn {
    background-color: #fff1f1;
    color: #ff4747;
    margin-left: 4px;
}
.nav-user-account .flyout-line {
    border-bottom: 1px solid #f2f2f2;
    display: block;
    margin: 10px 16px 0;
    height: 0;
}
.nav-global .flyout-quick-entry {
    margin-bottom: 8px;
}
.nav-global .user-account-port>a:after {
    content: "";
    position: absolute;
    top: 17px;
    right: 0;
    width: 8px;
    height: 5px;
    overflow: hidden;
}
.nav-global .flyout-quick-entry li a {
    display: block;
    padding: 0 16px;
    line-height: 32px;
    height: 32px;
    white-space: nowrap;
}
.nav-user-account:hover .user-account-main {
    display: block;
}
.nav-user-account{
    cursor: pointer;
}
.nav-user-account:hover .user-account-port {
    background-color: #fff;
    margin: -1px -1px 0;
    height: 35px;
    border: 1px solid #e8e8e8;
    border-bottom: 0;
    z-index: 33;
    box-shadow: 0 -4px 8px 0 rgb(0 0 0 / 4%);
}
.header .logo {
    width: 25%;
    padding-bottom: 10px;
}
.header .search{
    width: 55%;
}
.header .header-items{
    width: 20%;
