}


/* Home Page  First Screen */

.home-page{
    background-color: #f2f2f2;
}
.home-page .first-screen{
    padding: 20px 0px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}
.home-page .first-screen .categories{
    width: 25%;
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
}
.home-page .first-screen .advertise{
    width: 55%;
    margin: 0px 15px;
    border-radius: 8px;
}
.home-page .first-screen .account-main{
    width: 20%;
    background-color: #fff;
    background-image: url("https://ae01.alicdn.com/kf/He1ffbc1d340f4be8a0a6d07641c58180t.png_.webp");
    background-repeat: no-repeat;
    background-size: 100% 109px;
    padding: 20px;
    border-radius: 8px;
}

.home-page .first-screen .categories .categories-title a{
    display: flex;
    justify-content: left;
    color: inherit;
    padding-bottom: 10px;
    margin-left: 5px;
}
.home-page .first-screen .categories .categories-title a:hover{
    text-decoration: none;
}
.home-page .first-screen .categories .categories-title a h3{
    font-size: 14px;
    font-weight: 700;
    padding-left: 20px;
}

.home-page .first-screen .advertise{
    display: flex;
    flex-direction: column;
}
.home-page .first-screen .advertise .banner{
    height: 275px;
    border-radius: 8px;
}
.home-page .first-screen .advertise .banner img{
    width: 100%;
    height: 275px !important;
    object-fit: cover;
    border-radius: 10px;
}
.home-page .first-screen .advertise .banner .slick-dots{
    position: absolute;
    bottom: 7px;
}
.home-page .first-screen .advertise .banner .slick-dots li button:before {
    content: " ";
    position: absolute;
    top: 0;
    left: 0;
    width: 6.4px;
    height: 6.4px;
    border-radius: 50%;
    text-align: center;
    background: #fff;
}
.home-page .first-screen .advertise .banner .slick-dots li button {
    font-size: 0;
    line-height: 0;
    display: block;
    width: 6.4px;
    height: 6.4px;
    padding: 0;
    cursor: pointer;
    color: #fff;
    border: 0;
    outline: 0;
    background: 0 0;
}
.home-page .first-screen .advertise .banner .slick-dots li.slick-active button:before {
    width: 12.8px;
    border-radius: 3.2px;
    opacity: 1;
    background: #ff472e;
}

.slick-next {
    right:15px;
}
.slick-prev {
    left: 15px;
    z-index: 99;
}

.home-page .first-screen .categories .categories-list-box ul a{
    color: #000;
    text-decoration: none;
}
.home-page .first-screen .categories .categories-list-box ul a:hover{
    color:#e62e04;
    font-weight: 600;
}
.home-page .first-screen .categories .categories-list-box ul .item{
    display: flex;
    flex-direction: row;
    padding: 6px 0px;
}
.home-page .first-screen .categories .categories-list-box ul .item .categories-icon{
    width: 30px;
    height: 24px;
}
.slick-dots li{
    width: 15px;
    height: 15px;
}

